.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 900s linear;
          animation: App-logo-spin infinite 900s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.main{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 95%;
  margin: 0 auto;
}

.shop{
  border: 1px solid black;
  width: 60%;
  min-height: 500px;
  -webkit-box-shadow: 1px 1px 3px black;
          box-shadow: 1px 1px 3px black;
  margin: 5%;
}

.products{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.product{
  border: 1px solid black;
  width: 60%;
  min-height: 100px;
  -webkit-box-shadow: 1px 1px 3px black;
          box-shadow: 1px 1px 3px black;
  margin: 5%;
}

.product h3{
  margin-bottom: 5px;
  margin-top: 10px;
}

.cart{
  border: 1px solid black;
  width: 30%;
  min-height: 500px;
  -webkit-box-shadow: 1px 1px 3px black;
          box-shadow: 1px 1px 3px black;
  margin: 5%;
}


